import React from 'react';
import ReactDom from 'react-dom';
import './index.css';
// import App from './App';
// import * as serviceWorker from './serviceWorker.js';
import Table from './app/components/table.js';
import Header from './app/components/header.js';
import ElementDetail from './app/components/detail.js';

import detailData from './data.js';
var elements_detail_data = detailData();

// App
class App extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            clickElement:0,
        }
    }

    handleElemClick(n){
        // console.log('handleElemClick ->',n)
        this.setState({
            clickElement:n
        })
    }

    handleClickMasking(){
        this.setState({
            clickElement:0
        })
    }

    detail(){
        if(this.state.clickElement > 0 & this.state.clickElement <= 120){
            return (
                <ElementDetail
                    detailData = {elements_detail_data[this.state.clickElement]}
                    handleClickMasking = {()=>this.handleClickMasking()}
                />
            )
        }
    }
    
    render(){
            
        return(
            <div className="app-ui-wrapper">
                <Header/>
                <Table
                    handleElemClick={(n)=>this.handleElemClick(n)}
                />
                {this.detail()}
            </div>

        )
    }
}




// ===============================
ReactDom.render(<App/>, document.getElementById('root'));