import React from 'react';

import './detail.css';

function imageCard(cardData){
    let cardIamge = require(`../../file/elements/${cardData[0]}.jpg`);
    var imageCard = (
        <div className="detail-image-card-wrapper-wrapper">
            <div className="detail-image-card-wrapper">
                <img className="detail-image-card-iamge" src={cardIamge}></img>
                <div className="detail-image-card-filter" style={{'border-bottom':'2px solid #e9db39'}}></div>
                <div className="detail-image-card-info-wrapper">
                    <span className="detail-image-card-symbol">{cardData[0]}</span>
                    <span className="detail-image-card-zh">{cardData[1]}</span>
                    <span className="detail-image-card-number">{cardData[4]}号</span>
                    <span className="detail-image-card-en">{cardData[2]}</span>
                    <span className="detail-image-card-atomic-mass">{cardData[5]}</span>
                </div>
            </div>
        </div>
    )

    return imageCard;
}

function detailInfo(unitData){
    // unitData [[],[],]
    let item = [];
    console.log('detail info ->',unitData)
    for(let i=0; i<unitData.data.length; i++){
        item.push((
            <div className="unit-item-box-wrapper">
                <span className="unit-item-title">{unitData.data[i][0]}</span>
                <span className="unit-item-data">{unitData.data[i][1]}</span>
            </div>
        ))
    }

    var unit = (
        <div className="">
            <div className="unit-title-box-wrapper">
                <div className="unit-title-icon-wrapper">
                    <img className="unit-title-fold-icon" src={require('../../file/icon/fold_l_b.png')}></img>
                    <span>{unitData.title}</span>
                </div>
                <img className="unit-title-cion" src={require('../../file/icon/原子性质_b.png')}></img>
            </div>
            {item}
        </div>
    )
    return unit;
}

function spectral(data){
    var spectral = (
        <div>

        </div>
    )
    return spectral;
}

function electronShells(data){

}

class ElementDetail extends React.Component{
    constructor(props){
        super(props);
        this.state = {
        };
    }

    render(){
        let detail = [];
        console.log('detail ->',this.props.detailData)
        for(let i=0; i<this.props.detailData.length;i++){
            if(this.props.detailData[i].is == "unit"){
                console.log('else ->', this.props.detailData[i])
                detail.push(
                    detailInfo(this.props.detailData[i])
                );
            }else if(this.props.detailData[i].is == "imageCard"){
                console.log('else ->', this.props.detailData[i])
                detail.push(
                    imageCard(this.props.detailData[i].data)
                );
            }else if(this.props.detailData[i].is == "template"){
                if(this.props.detailData[i].name == "atomStructure"){
                    detail.push(
                        electronShells(this.props.detailData[i].data)
                    );
                }else if(this.props.detailData[i].name == "spectralblock"){
                    detail.push(
                        spectral(this.props.detailData[i].data)
                    );
                }
            }else{
                console.log('End else ->', this.props.detailData[i])
            }
        }

        console.log('detail ->->',detail)

        return(
            <div className="detail-wrapper-wrapper"
                onClick={()=>this.props.handleClickMasking()}
            >
                <div className="detail-wrapper"
                    onClick={(e)=>e.stopPropagation()}
                >
                    {detail}
                </div>
            </div>
        )
    }
}

export default ElementDetail;